.spells-factors {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 50px;

  &__title {
    margin-bottom: 25px;
  }

  &__case {
    display: flex;
    justify-content: space-around;
    height: 85%;
    width: 1200px;
  }

  &__buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
  }

  &__button {
    writing-mode: vertical-lr;
    text-orientation: upright;
    font-weight: bold;
    cursor: pointer;
    border: 1px solid white;
    border-radius: 25px;
    padding: 10px 5px;
  }

  &__button:hover {
    animation-direction: normal;
    animation: discoloration 0.4s forwards;
  }

  &__info {
    display: flex;
    height: 100%;
    width: calc(100% - 90px);
  }

  &__silhouette_case {
    position: relative;
    height: 100%;
    width: calc(100% - 300px);
    // border: 1px solid white;
  }

  &__silhouette {
    position: absolute;
    height: 100%;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0.5;
  }

  &__silhouette_basic {
    z-index: -1;
    opacity: 1;
  }

  &__silhouette_standout {
    opacity: 1;
    z-index: 5;
  }

  &__data {
    width: 300px;
    height: 100%;
    // border: 1px solid white;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
  }

  &__data_block {
    display: flex;
    align-items: center;
    gap: 15px;
    border: 1px solid white;
    border-radius: 15px;
    padding: 10px 15px;
    height: 50px;
    margin: 5px 0px;
    position: relative;
    cursor: pointer;
  }

  &__data_block:hover &__data_description {
    display: block;
  }
  &__data_block:hover &__silhouette_picture {
    opacity: 1;
  }

  &__data_icon {
    height: 100%;
  }

  &__data_description {
    position: absolute;
    transform: translateX(-105%) translateY(-50%);
    top: 50%;
    left: 0;
    border: 1px solid white;
    border-radius: 15px;
    padding: 5px;
    max-width: 300px;
    display: none;
    background-color: #010522;
    z-index: 995;
  }
}

@media screen and (max-width: 1200px) {
  .spells-factors {
    &__case {
      display: flex;
      justify-content: space-around;
      height: 85%;
      width: 100%;
    }

    &__silhouette_case {
      width: calc(100% - 200px);
    }

    &__data {
      width: auto;
      padding: 0 15px;
    }
  }
}

@media screen and (max-width: 600px) {
  .spells-factors {
    height: auto;

    &__case {
      height: 100%;
    }

    &__buttons {
      justify-content: center;
      gap: 20px;
    }

    &__info {
      height: auto;
    }

    &__data {
      padding: 0;
      align-items: flex-end;
      justify-content: center;
      flex: 1;
    }

    &__silhouette {
      height: auto;
      max-height: 40vh;
      width: auto;
      top: 50%;
      transform: translateY(-50%) translateX(-50%);
    }
  }
}