.introduction {
  overflow: hidden;
  width: 100%;

  &__video_case {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    overflow: hidden;
    z-index: -2;
  }

  &__video {
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    object-fit: cover;
  }

  &__video_explanation {
    width: 400px;
    height: 400px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) translateY(50%);
    opacity: 0.1;
  }

  &__case {
    width: 100%;
    height: calc(100vh - 80px);
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  &__left {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }

  &__game_marks {
    display: flex;
    align-items: center;
    gap: 25px;
  }

  &__game_name {
    height: 200px;
  }

  &__game_symbol {
    height: 150px;
  }

  &__big-text {
    white-space: pre-line;
  }
}

@media screen and (max-width: 1100px) {
  .introduction {
    &__game_name {
      height: 150px;
    }
  }
}

@media screen and (max-width: 800px) {
  .introduction {
    &__big-text {
      display: none;
    }
  }
}

@media screen and (max-width: 600px) {
  .introduction {
    &__game_name {
      height: auto;
      max-height: 150px;
      max-width: calc(90% - 100px);
    }

    &__game_marks {
      justify-content: center;
    }

    &__left {
      margin: 0 30px;
    }
  }
}